body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif,Helvetica, Tahoma;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e4d7fd;
  min-height: 100vh;
  background-image: url(../img/bg.jpg);
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #FF4081;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#home,
#list,
#detail,
#content {
  padding-bottom: 40px;
  max-width: 1200px;
  overflow-x: hidden;
  margin: auto;
}

#mainbox {
  padding-bottom: 120px;
}

#newlist {
  max-width: 1200px;
  margin: auto;
  padding-bottom: 120px;
}

#footers {
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: rgba(227, 164, 242, 0.8);
  line-height: 30px;
  font-size: 20px;
  width: 100%;
  display: block;
  text-align: center;
}

#footers .btn {
  width: 210px;
  display: inline-block;
  line-height: 40px;
  text-align: center;
  font-size: 12px;
  background-color: #2d1856;
  color: #fff;
  padding: 0 10px;
  border-radius: 20px;
  margin: 20px 40px;
  border: 1px solid #333;
  cursor: pointer;
}

.slick-slider img {
  display: block;
  width: 100%;
}

/* .adsbygoogle {
  display: none;
} */

.btn {
  padding: 10px 20px;
  border: 1px solid #FF4081;
  display: inline-block;
  position: relative;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  overflow: hidden;
}

.btn:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  width: 1px;
  top: 0;
  bottom: 0;
  margin: auto;
  background: #FF4081;
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  z-index: 0;
  opacity: 0;
}

.btn span {
  position: relative;
  z-index: 5;
}

.btn:hover {
  color: #FFF;
}

.btn:hover:before {
  -webkit-transform: scaleX(200);
  -ms-transform: scaleX(200);
  transform: scaleX(200);
  opacity: 1;
}


header {
  overflow: hidden;
